// @ts-nocheck
import * as React from "react";
import { useDispatch, useSelector } from "react-redux";
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { Button, CardActionArea, CardActions } from "@mui/material";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Divider from "@mui/material/Divider";
import ListItemText from "@mui/material/ListItemText";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import Avatar from "@mui/material/Avatar";
import PhoneAndroidRoundedIcon from "@mui/icons-material/PhoneAndroidRounded";
import AccessibilityRoundedIcon from "@mui/icons-material/AccessibilityRounded";
import CreditCardRoundedIcon from "@mui/icons-material/CreditCardRounded";
import CssBaseline from '@mui/material/CssBaseline';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Paper from '@mui/material/Paper';
import Fab from '@mui/material/Fab';
import ListSubheader from '@mui/material/ListSubheader';
import MenuIcon from '@mui/icons-material/Menu';
import AddIcon from '@mui/icons-material/Add';
import SearchIcon from '@mui/icons-material/Search';
import MoreIcon from '@mui/icons-material/MoreVert';
import { styled } from "@mui/material/styles";
import AppBar from "@mui/material/AppBar";
import logo from "../../../asset/guihon.png";
import { Link } from "react-router-dom";
import VerifiedUserRoundedIcon from "@mui/icons-material/VerifiedUserRounded";
import ExitToAppRoundedIcon from "@mui/icons-material/ExitToAppRounded";
import CvView from "./CvView"
import axios from "axios"
import path from "../../../const/path"
import { getSeeker } from "../../../redux/slices/seekerSlice/seekerSlice";

const StyledFab = styled(Fab)({
  position: "absolute",
  zIndex: 1,
  top: -30,
  left: 0,
  right: 0,
  margin: "0 auto",
});

function Users() {
  const dispatch = useDispatch();
  const loading = useSelector((state) => state.seeker.loading);
  const seekers = useSelector((state) => state.seeker.seeker);

    React.useEffect(() => {
      dispatch(getSeeker());
    }, [dispatch]);

  

  return (
    <div>
      <div>
        {loading && (
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        )}
        {!loading && (
          <div className="mb100">
            {seekers.map((user, index) => (
              <User user={user} key={`${index}`} />
            ))}
          </div>
        )}
      </div>
      <AppBar position="fixed" color="primary" sx={{ top: "auto", bottom: 0 }}>
        <Toolbar>
          <IconButton color="inherit" aria-label="open drawer">
            <Link to={"/"}>
              <img src={logo} className="logo" alt="logo" />
            </Link>
          </IconButton>
          <Link to="/register">
            <StyledFab color="primary" aria-label="add">
              <AddIcon />
            </StyledFab>
          </Link>
          <Box sx={{ flexGrow: 1 }} />
          <IconButton color="inherit">
            <VerifiedUserRoundedIcon />
          </IconButton>
            <IconButton color="inherit">
              <Link to={"/"} className="white">
                <ExitToAppRoundedIcon />
              </Link>
            </IconButton>
        </Toolbar>
      </AppBar>
    </div>
  );
}


function User({user}){
    return (
      <Card sx={{ maxWidth: 445, mb: 3 }}>
        <CardActionArea>
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              {user.name} {user.surname}
            </Typography>
            <List
              sx={{ width: "100%", maxWidth: 360, bgcolor: "background.paper" }}
            >
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <PhoneAndroidRoundedIcon />
                </ListItemAvatar>
                <ListItemText
                  primary={user.phone}
                  secondary={
                    <React.Fragment>
                      <Typography
                        sx={{ display: "inline" }}
                        component="span"
                        variant="body2"
                        color="text.primary"
                      ></Typography>
                    </React.Fragment>
                  }
                />
              </ListItem>
              <Divider variant="inset" component="li" />
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <CreditCardRoundedIcon />
                </ListItemAvatar>
                <ListItemText
                  primary={user.cni}
                  secondary={
                    <React.Fragment>
                      <Typography
                        sx={{ display: "inline" }}
                        component="span"
                        variant="body2"
                        color="text.primary"
                      ></Typography>
                    </React.Fragment>
                  }
                />
              </ListItem>
              <Divider variant="inset" component="li" />
              <ListItem alignItems="flex-start">
                <ListItemAvatar>
                  <AccessibilityRoundedIcon />
                </ListItemAvatar>
                <ListItemText
                  primary={user.age + " ans"}
                  secondary={
                    <React.Fragment>
                      <Typography
                        sx={{ display: "inline" }}
                        component="span"
                        variant="body2"
                        color="text.primary"
                      ></Typography>
                    </React.Fragment>
                  }
                />
              </ListItem>
            </List>
          </CardContent>
        </CardActionArea>
        <CardActions>
          <CvView title={`${user.name} ${user.surname} `} cv={`${path.api_url}/file/${user.cv}`} />
          <RemoveBtn seek={user.id} />
        </CardActions>
      </Card>
    );
}

function RemoveBtn({seek}){
  const dispatch = useDispatch();
  const [load, setLoad] = React.useState(false)

  const handleRemove = ()=>{
    setLoad(true)
    axios.delete(`${path.api_url}/seeker/${seek}`)
    .then((res)=>{
      setLoad(false)
      dispatch(getSeeker());
    }).catch(err=>{
      setLoad(false)
    })
  }
  return (
    <Button size="small" color="error" onClick={handleRemove}>
      {!load && <span>Retirer</span>}
      {load && <span>Supression...</span>}
    </Button>
  );
}

export default Users

